/**
 * 
 */
import WSAction from '@/api/ws_action.js'

export default {
	//
	createRoom:function(vue, fn){
		//loading
		vue.$vux.loading.show({text:""})
		vue.$http.get(process.env.WEB_ADDR+'/room/create',{withCredentials:true}).then(res=>{
			vue.$vux.loading.hide();
			var resdata = res.data;
			if (!resdata.status) {
				//
				vue.$vux.toast.text(resdata.message);
			} else {
				//roomno
				fn(resdata.data);
			}
		}, err=>{
			vue.$vux.loading.hide();
			vue.$vux.alert.show({title:'', content:err.message})
		});
	},
	//request
	outRoom:function(vue, fn) {
		if (!vue.user.uid) {
			vue.$vux.toast.text('');
			return;
		}
		vue.$vux.loading.show({text:''})
		vue.$http.get(process.env.WS_REQ+'?action=out&uid='+vue.user.uid, {withCredentials:false}).then(res=>{
			vue.$vux.loading.hide()
			var resdata = res.data;
			if (!resdata.status) {
				//
				vue.$vux.toast.text(resdata.message);
			} else {
				//
				fn();
			}
		}, err=>{
			vue.$vux.loading.hide();
			vue.$vux.alert.show({title:'', content:err.message})
		});
	},
	//
	enterRoom:function(vue, room_no, fn) {
		if (!vue.user.uid) {
			vue.$vux.toast.text('');
			return;
		}
		vue.$vux.loading.show({text:''})
		vue.$http.get(process.env.WS_REQ+'?action=enter&uid='+vue.user.uid+'&room_no='+room_no, {withCredentials:false}).then(res=>{
			vue.$vux.loading.hide()
			var resdata = res.data;
			if (!resdata.status) {
				//
				vue.$vux.toast.text(resdata.message);
			} else {
				//
				fn && fn();
			}
		}, err=>{
			vue.$vux.loading.hide();
			vue.$vux.alert.show({title:'', content:err.message})
		});
	},
	//
	roomInfo:function(vue, room_no, fn, errfn) {
		vue.$http.get(process.env.WEB_ADDR+'/room/info?room_no='+room_no,{withCredentials:true}).then(res=>{
			var resdata = res.data;
			if (!resdata.status) {
				//
				vue.$vux.alert.show({title:'', content:resdata.message});
				errfn && errfn()
			} else {
				//
				fn(resdata.data);
			}
		}, err=>{
			vue.$vux.alert.show({title:'', content:err.message})
		});
	},
	//
	roomUsers:function(vue, room_no, fn) {
		vue.$http.get(process.env.WEB_ADDR+'/room/users?room_no='+room_no,{withCredentials:true}).then(res=>{
			var resdata = res.data;
			if (!resdata.status) {
				//
				vue.$vux.alert.show({title:'', content:resdata.message});
			} else {
				//
				fn(resdata.data);
			}
		}, err=>{
			vue.$vux.alert.show({title:'', content:err.message})
		});
	},
	//
	prepareCmd:function(vue, room_no, fn) {
		if (!vue.user.uid) {
			vue.$vux.toast.text('');
			return;
		}
		vue.$vux.loading.show()
		vue.$http.get(process.env.WS_REQ+'?action=prepare&uid='+vue.user.uid+'&room_no='+room_no, {withCredentials:false}).then(res=>{
			vue.$vux.loading.hide()
			var resdata = res.data;
			if (!resdata.status) {
				//
				vue.$vux.alert.show({title:'',content:resdata.message});
			} else {
				//
				fn && fn();
			}
		}, err=>{
			vue.$vux.loading.hide();
			vue.$vux.alert.show({title:'', content:err.message})
		});
	},
	//
	startCmd:function(vue, room_no, fn) {
		if (!vue.user.uid) {
			vue.$vux.toast.text('');
			return;
		}
		vue.$vux.loading.show()
		vue.$http.get(process.env.WS_REQ+'?action=start&uid='+vue.user.uid+'&room_no='+room_no, {withCredentials:false}).then(res=>{
			vue.$vux.loading.hide()
			var resdata = res.data;
			if (!resdata.status) {
				//
				vue.$vux.alert.show({title:'',content:resdata.message});
			} else {
				//
				fn && fn();
			}
		}, err=>{
			vue.$vux.loading.hide();
			vue.$vux.alert.show({title:'', content:err.message})
		});
	},
	/**
	 * 
	 * @param vue
	 * @param room_no
	 * @param runfn , max, data
	 * @param resultfn rankdata
	 * @returns
	 */
	listScores:function(vue, room_no, runfn, resultfn) {
		vue.$http.get(process.env.WEB_ADDR+'/room/scores?room_no='+room_no+'&uid='+vue.user.uid, {withCredentials:false}).then(res=>{
			var resdata = res.data
			if(resdata.status == 1) {
				//
				runfn && runfn(resdata.data.rank, resdata.data.result, resdata.data.max, resdata.data.ranks, resdata.data.min)
			} else if(resdata.status == 2) {
				//
				resultfn && resultfn(resdata.data.rank, resdata.data.result, resdata.data.max, resdata.data.ranks, resdata.data.min)
			}
		})
	},
	//
	shakePhone:function(vue, WSAction) {
		//bind
		this.bind = function() {
			if (window.DeviceMotionEvent) {
				window.addEventListener('devicemotion', deviceMotionHandler, false);
			} else {
				vue.$vux.alert.show({title:'',content:''});
			}
		}
		
		//onbind
		this.onbind = function() {
			window.removeEventListener('devicemotion', deviceMotionHandler)
		}
		
		var SHAKE_THRESHOLD = 3000;
	    var last_update = 0;
	    var y = 0; var z=0; var last_x=0; var last_y=0; var last_z=0;var x=0;
		function deviceMotionHandler(eventData) {
	        var acceleration = eventData.accelerationIncludingGravity;
	        var curTime = new Date().getTime();
	        if ((curTime - last_update) > 100) {
	            var diffTime = curTime - last_update;
	            last_update = curTime;
	            x = acceleration.x;
	            y = acceleration.y;
	            z = acceleration.z;
	            var speed = Math.abs(x + y + z - last_x - last_y - last_z) / diffTime * 10000;
	            if (speed > SHAKE_THRESHOLD) {
	            		WSAction.play(vue.ws, vue.user.uid)
	            }
	            last_x = x;
	            last_y = y;
	            last_z = z;
	        }
	    }
	}
}


